.Carditem{
    display: flex;
    flex-direction: row;
}
.cardBody{
    display: flex;
    flex-direction: column;
    width: 700px;
    height: 300;
    background-color: white;
    align-items: center;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    padding: 5px;
    box-shadow: 0px 20px 25px -10px rgba(0,0,0,0.5);
    cursor: pointer;
	transition: 0.3s all ease-in-out;
}

.cardBody:hover{
    box-shadow: 2px 2px 15px #563d7c;
	transform: translatey(10px)  ;
}

.cardInfo{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: auto;
    margin: 5px;
 
}
.cardTitle p{
    width: 300px;
    background-color: gray;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 16px;
    display: block;
    margin-bottom: 6px;
    text-transform:uppercase;
    color: black;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.cardInfoImg{
    border:1px solid black;
    width: 300px;
    margin-right: 25px;
    margin-left:5px
    }

.cardDescription{
    flex-direction: column;
    width: 300px;
}


.descriptionText {
    padding-top: 5px;   
    height: 120px;
    font-family: "Poppins", sans-serif;
    text-transform:uppercase;
    background-color: gray;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
   
 
}

.descriptionText p {
    
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 5px;
}
.descriptionText ::marker{
    color:rgb(255, 174, 0);
}
.descriptionText li {
    text-transform: capitalize;
    color:white;
    float: left;
    font-size: 12px;
    line-height: 20px;
    margin-left: 15px;
    }
.cardFooter{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 620px;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    padding: 5px;
    flex-direction: row;
    margin: 5px;
    
}
.basketWrapper {
    display: flex;
    flex-direction: row;
}
.cardExplore-btn  {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 150px;
    height: 33px;
    white-space: nowrap;
    align-content: center;
    
    }
    .cardExplore-btn p{
        margin-top:15px;
        margin-left:5px;
        font-size: 12px;
        font-weight: 700;
        color:white;
    }
    
    .widget-shopping-icon  {
        font-size: 15px;
        margin-right: 5px;
        color: white;
       
     }

.detailLink{
    font-family: "Poppins", sans-serif;
    margin-left: 5px;
    height: 32px;
    color: black;
    text-decoration: none;
    white-space: nowrap;
    padding: 5px;
}

.combo-action{
    background-color: #c6ff00;
    color: #282828;
    font-size: .7em;
}
.combo-action:hover{
    background-color: #efefef
}

